import { Injectable } from '@angular/core';
import { Http, Headers } from "@angular/http";
import { Observable } from 'rxjs/Observable';
import { List } from '../models/list';
import 'rxjs/add/operator/map';
@Injectable()
export class ListService {
  private serverApi = `http://localhost:3000`;
  constructor(private http: Http) { }

  public getAllList(): Observable<List[]> {
    let URI = `${this.serverApi}/bucketlist/`;
    return this.http.get(URI).map(res => res.json()).map(res => <List[]>res.lists);
  }

  public deleteList(listId: string) {
    let URI = `${this.serverApi}/bucketlist/${listId}`;
    let headers = new Headers;
    headers.append('Content-Type', 'application/json');
    return this.http.delete(URI, { headers: headers }).map( res => res.json);
  }

  public addList(list: List) {
    let URI = `${this.serverApi}/bucketlist/`;
    let headers = new Headers;
    let body = JSON.stringify({ title: list.title, description: list.description, category: list.category });
    console.log(body);
    headers.append('Content-Type', 'application/json');
    return this.http.post(URI, body, { headers: headers })
              .map(res => res.json());
  }

  public updateList(list: List) {
    let URI = `${this.serverApi}/bucketlist/${list._id}`;
    let headers = new Headers;
    let body = { title: list.title, description: list.description, category: list.category };
    headers.append('Content-Type', 'application/json');
    return this.http.put(URI, body, { headers: headers })
              .map(res => res.json());
  }
}



